.item_body {
    border: 0.2em solid black;
    border-radius: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2%;
  
    width: 90%;

  
    max-height: 8%;
    margin-top: 2%;
  
    transition: transform 0.2s linear;
  }
  
  .item_link {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 55%;

  }
  
  .item_body:hover {
    transform: translate3d(0px, -3%, 0px);
  }
  
  .images {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width:35%;
  }
  
  .image {
    width: 25%;
  }
  
  .info {
    color: black;
  
    display: flex;
    flex-direction: column;
    padding-left: 5%;
    width: 50%;
    font-size: 1.5em;
  }
  
  .info_name {
    background: rgb(164, 212, 91);
    width: 100%;

  }
  
  .cross {
    display: flex;
    width: 30px;
    height: 30px;
    transition: transform 0.3s linear;
  }
  
  .cross:hover {
    transform: rotate(360deg);
  }
  
  .cross_link {
    display: flex;
    width: 3%;
  }
  
  .AppendReduseCross {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 10%;
  }
  .AppendReduse{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .plus_bkgr {
    display: flex;
    height: 85%;
    width: 90%;
    align-items: center;
    justify-content: center;
    background: rgb(103, 185, 185);
  }
  
  .plus {
    height: 100%;
    width: 100%;
    transition: background 0.1s linear;
  }
  
  .plus:hover {
    background: rgb(226, 225, 225);
  }
  
  .minus_bkgr {
    display: flex;
    align-items: center;
    justify-content: center;

    width: 90%;
    background: rgb(185, 103, 103);
  }
  
  .minus_link {
    display: flex;
    justify-content: center;
    width: 100%;
  }
  
  .minus {
    height: 100%;
    width: 100%;
    transition: background 0.1s linear;
  }
  
  .minus:hover {
    background: rgb(226, 225, 225);
  }